  name: 'HomePage',

  components: {
    SearchDropdown,
    DynamicButton,
    CardIconBox,
    BrandsList,
    // Search_Algolia,
  },

  data() {
    return {
      title: 'Home',
      openURL,
    };
  },

  computed: {
    ...mapState(useAsana, ['urls']),
    ...mapState(useUser, ['user', 'selectedOrg', 'selectedOrgIsMaintenance']),
    ...mapState(useGrowmodo, ['statuses', 'externalLinks']),

    miniLinks1() {
      return [
        {
          label: 'Task Directory',
          icon: 'icon-list',
          action: () => this.$root.toggleRequest?.('task'),
        },
        {
          label: 'Help Center',
          icon: 'icon-message-question-square',
          action: () => openURL(this.externalLinks?.help_link),
        },
      ];
    },

    miniLinks2() {
      return [
        {
          label: 'Book a Call',
          icon: 'icon-phone-call-01',
          action: () => this.$router.push('/book-a-call'),
        },
        {
          label: 'Get a Gift',
          icon: 'icon-gift-01',
          action: () => this.$router.push('/gift'),
        },
      ];
    },

    userName() {
      return capitalize(this.user?.firstname || '');
    },

    orgStatus() {
      return this.statuses?.[this.selectedOrg?.subscriptions?.status];
    },
  },

  async mounted() {
    this.$root.setMeta({ title: this.title });
  },

  methods: {
    ...mapActions(useUser, ['isOrgOwner', 'isOrgAdmin', 'isOrgBiller']),
  },
